import React from 'react';
import './Listitem.css';
import PropTypes from 'prop-types';
export const ListItem = ({src,name,desc}) => (
    <li className="listItemWrapper">
        <img className="listItemImage" src={src}/>
        <div>
            <h2 className="ListItemName">{name}</h2>
            <p className="ListItemDescription">{desc}</p>
            <button className="ListItemButton">Get from facebook page</button>
        </div>
    </li>
);
ListItem.propTypes = {
    src: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    desc: PropTypes.string,
}
ListItem.defaultProps = {
    desc: 'Niestety nie ma tekstu'
}